<template>
    <v-layout row="row"
        wrap="wrap">
        <v-flex xs12="xs12"
            sm12="sm12"
            md6="md6"
            offset-xs0="offset-xs0"
            offset-sm0="offset-sm0"
            offset-md3="offset-md3">
            <div class="white elevation-2">
                <v-toolbar flat="flat"
                    dense="dense"
                    class="cyan"
                    dark="dark">
                    <v-toolbar-title>Create poll</v-toolbar-title>
                </v-toolbar>
                <div class="pl-4 pr-4 pt-2 pb-2">
                    <v-form autocomplete="off"
                        ref="form"
                        lazy-validation="lazy-validation">
                        <v-text-field v-model="question"
                            data-vv-name="question"
                            v-validate="{required: true}"
                            :error-messages="errors.collect('question')"
                    
                            label="Topic"></v-text-field>
                        <h3>Poll options</h3>
                        <div v-for="(value, i) in options"
                            :key="i">
                            <v-text-field v-model="options[i]"
                                append-icon="clear"
                                :append-icon-cb="() => removePoll(i)"></v-text-field>
                        </div>
                        <v-text-field label="Add a poll option"
                            v-model="option"
                            data-vv-name="option"
                            append-icon="add"
                            :append-icon-cb="addPoll"></v-text-field>

                        <v-alert v-if="error"
                            type="error"
                            :value="true"
                            v-html="error" />
                        <v-btn class="cyan"
                            :disabled="errors.items.length > 0 || options.length === 0"
                    
                            @click="createPoll">Start Poll</v-btn>
                    </v-form>
                </div>
            </div>
            <v-snackbar v-if="success"
                :timeout="3000"
                :top="true"
                :bottom="false"
                :right="false"
                :left="false"
                :multiline="false"
                :vertical="false"
                v-model="success">
                The poll is created
                <v-btn flat="flat"
                    color="pink"
                    @click.native="success = false">Close</v-btn>
            </v-snackbar>
        </v-flex>
    </v-layout>
</template>

<script>
    import PollService from '@/services/PollService';

    export default {
        name: 'CreatePoll',
        data () {
            return { question: null, options: [], option: null, error: null, success: false };
        },
        watch: {
            option (newVal) {
                this.error = null;
            }
        },
        methods: {
            async createPoll (event) {
                if (event)
                    event.preventDefault();
                if (this.question && this.options.length > 0) {
                    try {
                        let poll = {
                            question: this.question,
                            choices: this.options
                        };
                        const response = await PollService.createPoll(poll);
                        this.success = true;
                        this.question = null;
                        this.options = [];
                        console.log(response);
                        // the response need to contains the poll list
                    } catch (error) {
                        this.error = error.response.data.error.msg || 'An error has occured, please try again later';
                    }
                } else {
                    this.error = 'You need to fill the poll options and the question in order to create poll';
                }
                this.$validator.reset();
                console.log('create poll');
            },
            removePoll (i) {
                console.log('remove poll i', i);
                this.options.splice(i, 1);
            },
            addPoll () {
                console.log('add poll');
                if (this.option !== null) {
                    let optionExisted = this.options.some(function (optionItem) {
                        return this.option.trim().toLowerCase() === optionItem.trim().toLowerCase();
                    }.bind(this));
                    if (optionExisted) {
                        this.error = "This option is already existed";
                    } else {
                        this.options.push(this.option);
                        this.option = null;
                    }
                }
            }
        }
    };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped="scoped">
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>


